html,
body,
#root {
    height: 100%;
}

* {
    transition: 200ms all ease-in;
}

@mixin flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    max-width: 1000px;
    margin: 0 auto;
    height: 100%;

.flex{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

    height: 100%;
}
img{
    height: 200px;
    width: 200px;
    margin-top:20px;
}

form {
    //   display: flex;
    @include flex-center;
    flex-direction: column;

    input {

    }

    button {

    }
}

.form-container{
    max-width: 680px;
    padding: 40px;
    background-color: #fff;
    box-shadow: 0 6px 30px rgba(0,0,0,.25);
    border-radius: 3px;
}
form{
    input{
        width: 100%;
        display: block;
        width: 600px;
        @media(max-width: 699px){
            width: 100%;
        }
        border: 1px solid pink;
        padding: 0px 10px;
        // border-radius: 40px;
        margin-bottom: 2px;
        height: 52px;
        font-size: 16px;
        // border-right: 0!important;
        background: #fff;
        border-color: #ff916f!important;

    }
    button{
        width: 100%;
        text-align: center;
        border-left: 0;
        padding: 12px 20px;
        height: 52px;
        font-weight: 600;
        background: #ff5722;
        color: #fff!important;
        border-color: #ff5722;
        &:hover{
            background: darken(#ff5722, 2%);
        }
        &:focus, &:visited{
            outline: none;
        }
    }
}